import { Component, Element, h, Method, Prop } from '@stencil/core';
import { SharedStyles } from '@utils/shared-styles';
import { Offcanvas } from 'bootstrap/dist/js/bootstrap.bundle';

@Component({
  tag: 'storm-offcanvas',
  styleUrl: 'storm-offcanvas.css',
  shadow: true,
})
export class StormOffCanvas {
  @Element() host: HTMLElement;
  @Prop() placement: 'top' | 'bottom' | 'start' | 'end' = 'end';
  @Prop() backdrop: boolean = false;
  @Prop() scrollable: boolean = false;

  private offCanvas: Offcanvas;
  private offCanvasRef: HTMLElement;

  async componentWillLoad() {
    await SharedStyles.applyShadowRootStylesAsync(this.host, 'material');
  }

  componentDidLoad() {
    this.offCanvas = new Offcanvas(this.offCanvasRef, {
      backdrop: this.backdrop,
      scroll: this.scrollable,
    });
  }

  @Method()
  async toggle() {
    await this.offCanvas.toggle();
  }

  @Method()
  async show() {
    await this.offCanvas.show();
  }

  @Method()
  async hide() {
    await this.offCanvas.hide();
  }

  render() {
    return (
      <div class={'offcanvas offcanvas-' + this.placement} ref={e => (this.offCanvasRef = e)}>
        <div class="offcanvas-header pb-0 pt-3">
          <div class="float-start ms-4">
            <slot name="header" />
          </div>
          <button class="btn btn-link text-dark p-0 fixed-plugin-close-button me-2" onClick={() => this.hide()}>
            <i class="material-icons">clear</i>
          </button>
        </div>
        <hr class="horizontal dark my-1" />
        <div class="offcanvas-body">
          <slot name="body" />
        </div>
      </div>
    );
  }
}
